.CreateTodoButton {
    background-color: aqua;
    box-shadow: 0px 5px 25px rgb(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;

    transform: rotate(0);
    transition: .3s ease;
    z-index: 10000;
}

.CreateTodoButton:hover {
    transform: rotate(360deg);
}